body {
  margin: 0;
}

#chart {
  height: calc(100vh - 100px);
}

#controls {
  position: relative;

  padding-left: 50px;

  height: 100px;
}

.control-container {
  position: absolute;

  top: 50%;
  transform: translateY(-50%);
}

.line {
  fill: none;
  stroke: #ff8a14;
  stroke-width: 3;
}

.dot {
  fill: #ff8a14;
  stroke: #fff;
}
